<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="orgEdit">
        <el-card>
            <el-form class="u-edit-from" :model="ruleForm" :rules="rules" label-position="left" label-width="160px">
                <el-form-item label="机构名称：" prop="userName">
                    <el-input v-model="ruleForm.name" placeholder="请输入机构名称"></el-input>
                    <el-input v-model="ruleForm.name" placeholder="请输入机构简称" style="marginLeft: 10px"></el-input>
                </el-form-item>
                <el-form-item label="机构性质：" prop="userName">
                    <el-input v-model="ruleForm.name" placeholder="请输入机构性质"></el-input>
                </el-form-item>
                <el-form-item label="机构性质：" prop="userName">
                    <RichText v-model="ruleForm.nature" :[isCptAdd]="250" />
                </el-form-item>
                <el-form-item label="所在城市：" prop="userName">
                    <CityCascader v-model="ruleForm.cityList" size="middle" style="display: inline-block" />
                    <el-input v-model="ruleForm.name" placeholder="请输入门牌号" style="marginLeft: 10px"></el-input>
                </el-form-item>
                <el-form-item label="联系人：" prop="userName">
                    <el-input v-model="ruleForm.name" placeholder="请输入联系人"></el-input>
                </el-form-item>
                <el-form-item label="手机号：" prop="userName">
                    <el-input v-model="ruleForm.name" placeholder="请输入手机号"></el-input>
                </el-form-item>
                <el-form-item label="固定电话：">
                    <el-input v-model="ruleForm.name" placeholder="请输入固定电话"></el-input>
                </el-form-item>
                <el-form-item label="发单权限：" prop="userName">
                    <el-radio v-model="ruleForm.radio" label="1">无</el-radio>
                    <el-radio v-model="ruleForm.radio" label="2">有(开通发单平台权限)</el-radio>
                </el-form-item>
                <el-form-item label="资质证书：" prop="userName">
                    <el-upload
                        action="https://jsonplaceholder.typicode.com/posts/"
                        list-type="picture-card"
                        :limit="5"
                        :on-preview="handlePictureCardPreview"
                        :on-remove="handleRemove">
                        <i class="el-icon-plus"></i>
                    </el-upload>
                    <el-dialog :visible.sync="dialogVisible">
                        <img width="100%" :src="dialogImageUrl" alt="">
                    </el-dialog>
                </el-form-item>
                <el-button type="primary">提交</el-button>
            </el-form>

        </el-card>
    </div>
</template>

<script>
    import RichText from '@/components/RichText/RichText'
    import CityCascader from '@/components/CityCascader/CityCascader'
    export default {
        data() {
            return {
                isAddPage: false,
                ruleForm: {
                    cityList: []
                },
                rules: {
                    userName: [
                        { required: true, message: '请输入活动名称', trigger: 'blur' },
                    ]
                },
                dialogImageUrl: '',
                dialogVisible: false

            }
        },
        computed: {
            isCptAdd() {
                return this.isAddPage ? 'height' : 'editPage'
            }
        },
        methods: {
            handleRemove(file, fileList) {
                console.log(file, fileList);
            },
            handlePictureCardPreview(file) {
                this.dialogImageUrl = file.url;
                this.dialogVisible = true;
            }
        },
        created() {
            console.log(this.$route)
            console.log(this.$route.name === 'OrgAdd')
            this.isAddPage = (this.$route.name === 'OrgAdd')
        },
        components: {
            RichText, CityCascader
        }
    }
</script>
